<template>
  <div class="url">
    <label>
      <input type="checkbox" v-model="dataRowItemOption.ko" @change="setDataRowItemOption" value="true" />Korean
    </label>
    <label>
      <input type="checkbox" v-model="dataRowItemOption.en" @change="setDataRowItemOption" value="true" />English
    </label>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object,
  },
  setup(props) {
    const store = useStore();
    // default value
    let dataRowItemOption = props.dataRow
      ? computed(() => props.dataRow.item.itemOption)
      : ref({
          en: false,
          ko: false,
        });

    store.commit('setDataRowItemOption', {
      index: props.index,
      dataRowItemOption: dataRowItemOption.value,
    });

    // 선택한 data type을 store에 저장
    const setDataRowItemOption = () => {
      store.commit('setDataRowItemOption', {
        index: props.index,
        dataRowItemOption: dataRowItemOption.value,
      });
      console.log('dataRowItemOption -->', store.getters.getDataRowItemOption(props.index));
    };

    return {
      dataRowItemOption,
      setDataRowItemOption,
    };
  },
});
</script>

<style lang="scss" scoped>
.url {
  align-items: center;
  display: flex;
  font-size: 0.75rem;

  label {
    display: flex;
    align-items: center;
